export interface ChallengeOobSelectDeviceData {
  devices: DeviceInformationData[];
}

export interface DeviceInformationData {
  deviceId: string;
  created: string;
  platform?: string;
  vendor?: string;
  model?: string;
  friendlyName?: string;
}

export type OobSelectDeviceRequestBody = Pick<DeviceInformationData, 'deviceId'>;

export type OobSelectDeviceEvent = Extract<DeviceInformationData, OobSelectDeviceRequestBody>;
